/**
 * 
 * @param {Number || null} dateTime 
 * @param {string} fmt yyyy:mm:dd|yyyy:mm|yyyymmdd|yyyymmdd hhMM,   
 * @returns  string
 */
export function timeFormat(dateTime = null, fmt = "yy-mm-dd") {
    //  dateTime null , 
    if (!dateTime) dateTime = Number(new Date());
    // 10 *1000
    if (dateTime.toString().length === 10) dateTime *= 1000;
    let date = new Date(dateTime);
    let ret;
    let opt = {
        "y+": date.getFullYear().toString(), // 
        "m+": (date.getMonth() + 1).toString(), // 
        "d+": date.getDate().toString(), // 
        "h+": date.getHours().toString(), // 
        "M+": date.getMinutes().toString(), // 
        "s+": date.getSeconds().toString() // 
        // 
    };

    for (let k in opt) {
        ret = new RegExp(`${k}`).exec(fmt); // fmtk
        if (ret) {
            // replace  fmtret[1]fmtpadStart
            fmt = fmt.replace(ret[0], ret[0].length === 1 ? (opt[k]) : (opt[k].padStart(ret[0].length, '0')))
        }
    }
    return fmt
}





/**
 * 
 * @param {String} dateTime 
 * @param {String || Boolam } format   || false
 * @returns 
 */
export function timeForm(dateTime = null, format = 'yyyy-mm-dd',startTimeStamp = Number(new Date())) {
    // null,
    if (!dateTime) dateTime = Number(new Date());
    // dateTime101313
    if (dateTime.toString().length === 10) dateTime *= 1000

    let timestamp = new Date(Number(dateTime))
    let timer = (startTimeStamp - timestamp) / 1000;

    // 5,"",
    let tips = '';
    switch (true) {
        case timer < 300:
            tips = '';
            break;
        case timer >= 300 && timer < 3600:
            tips = parseInt(timer / 60) + '';
            break;
        case timer >= 3600 && timer < 86400:
            tips = parseInt(timer / 3600) + '';
            break;
        case timer >= 86400 && timer < 2592000:
            tips = parseInt(timer / 86400) + '';
            break;
        default:
            // formatfalsexx
            if (format === false) {
                if (timer >= 2592000 && timer < 365 * 86400) {
                    tips = parseInt(timer / (86400 * 30)) + '';
                } else {
                    tips = parseInt(timer / (86400 * 365)) + '';
                }
            } else {
                tips = timeFormat(timestamp, format);
            }
    }
    return tips;
}