<template>
  <div class="footer">
    <footer>
      <div class="left" @click="showPopup">
        <van-icon size="1.6rem" name="edit" />
        
      </div>
      <div class="right" v-show="!is_relay">
        <!-- 0  -->
        <!-- :badge="article.comment > 0 ? article.comment : ''" -->
        <van-icon size="1.6rem" name="chat-o" :badge="article.comment || ''" />
        <!--  -->
        <van-icon
          @click="articleFavToggle"
          :color="article.is_fav ? 'red' : ''"
          size="1.6rem"
          name="star"
        />
        <!--  -->
        <van-icon
          @click="articleLikeToggle"
          size="1.6rem"
          name="like"
          :color="article.is_like ? 'red' : ''"
        />
        <!-- ------------------------------------------------ -->
        <van-icon size="1.6rem" name="share-o" @click="showShare = true" />

        <!-- <van-cell title="" @click="showShare = true" /> -->
        <van-share-sheet
          v-model="showShare"
          title=""
          :options="options"
          @select="onSelect"
        />

        <!-- ---------------------------------------------------------------------------- -->
      </div>
      <!-- {{ isYan }} -->
    </footer>
    <van-popup v-model="show" position="bottom">
      <div class="tanchu">
        <textarea
          ref="input"
          @keyup.enter="jianpan"
          v-model.trim="content"
          name="text"
          :placeholder="placeholder"
          id=""
          cols="30"
          rows="10"
        ></textarea>
        <span>{{ content.length }}/1000</span>
        <button @click="submitComment"></button>
      </div>
    </van-popup>
    <!-- <van-popup v-model="show"></van-popup> -->
  </div>
</template>

<script>
import activeToggle from "./activeToggle.js";

import { Popup, ShareSheet } from "vant";

import QRCode from "qrcode";

export default {
  name: "ArticleFooter",
  components: {
    [Popup.name]: Popup,
    [ShareSheet.name]: ShareSheet,
  },
  data() {
    return {
      show: false,

      content: "",

      // 
      showShare: false,
      options: [
        { name: "", icon: "wechat" },
        { name: "", icon: "weibo" },
        { name: "", icon: "link" },
        { name: "", icon: "poster" },
        { name: "", icon: "qrcode" },
        { name: "QQ", icon: "qq" },
      ],
    };
  },
  props: {
    article: {
      required: true,
    },
    // ID
    article_id: {
      required: true,
    },
    // ID
    user_id: {
      required: true,
    },
    placeholder: {
      default: "",
      type: String,
    },

    reply_comment_id: {
      // 0  1id
      default: "",
      type: String,
    },
    comment_type: {
      // 0  1
      default: 0,
      type: [String, Number],
    },

    preSendIndex: {
      default: 0,
      type: Number,
    },

    is_relay: {
      default: false,
    },
  },

  computed: {
    userInfo() {
      return this.$store.state.userModules.userInfo;
    },
  },
  mounted() {
    // console.log(this.userInfo);
    // console.log(this.$parent.$refs.commentsList.list);
  },
  methods: {
    ...activeToggle,

    jianpan() {
      this.submitComment();
    },

    // 
    getTitle() {
      let { title } = this.article;

      title += "    ";

      title = encodeURIComponent(title);

      return title;
    },
    getPic() {
      let _img = "http://toutiao.longxiaokj.com/a023e9a3909f21667185407164.gif";
      let {
        imageSrc: [img],
      } = this.article;

      //    
      img = img || _img;

      //   encodeURIComponent
      img = encodeURIComponent(img);

      return img;
    },

    getUrl() {
      // 
      let { article_id } = this;
      return encodeURIComponent(
        "https://static-d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/toutiao/index.html#/article?_id=" +
          article_id
      );
      // location.href  
      // 
    },

    // 

    // ---------------------
    shareWX() {
      console.log("shareWX");
      let url = decodeURIComponent(this.getUrl());

      QRCode.toDataURL(url)
        .then((url) => {
          console.log(url);
        })
        .catch((err) => {
          console.error(err);
        });
    },

    // ---------------------------------
    shareWB() {
      console.log("shareWB");

      let title = this.getTitle();
      let img = this.getPic();

      // 
      window.open(
        `https://service.weibo.com/share/share.php?title=${title}&pic=${img}`
      );
    },
    // QQ------------------------------------
    shareQQ() {
      console.log("shareQQ");
      let title = this.getTitle();
      let img = this.getPic();
      let url = this.getUrl();
      //
      // &title= -  - 
      // &summary=offer offer
      // &pics=https://lf3-cdn-tos.bytescm.com/obj/static/xitu_juejin_web/img/favicon-300x300.34d45a4.png
      // &site=

      window.open(
        `https://connect.qq.com/widget/shareqq/index.html?url=${url}&title=${title}&pics=${img}$site=`
      );
    },
    // --------------------------------------------------
    shareLink() {
      // 1.input
      let input = document.createElement("input");
      // 2.input
      input.value = "" + window.atob(this.article_id);
      // 3.input
      document.body.appendChild(input);
      // 4.
      input.select();
      // 5.
      document.execCommand("copy");
      // 6.input
      input.remove();

      this.$toast(" ");
    },
    // 
    poster() {
      this.$emit("render");
    },
    // 
    onSelect({ name }) {
      this.$toast(name);
      this.showShare = false;

      let shareMethods = {
        : this.shareWX,
        : this.shareWB,
        QQ: this.shareQQ,
        : this.shareLink,
        : this.poster,
      };

      shareMethods[name].call(this);
    },
  },
};
</script>

<style lang="less" scoped>
.footer {
  width: 100%;

  position: fixed;
  bottom: 0;
  border-top: 1px solid #ccc;
  left: 0;
}
footer {
  display: flex;
  justify-content: space-between;
  padding: 10px 20px;
  background: #fff;

  .left {
    display: flex;
    align-items: center;
    height: 38px;
    flex: 4;
    border-radius: 15px;
    background: #f3f3f3;
    justify-content: center;
  }
  .right {
    flex: 6;
    display: flex;
    justify-content: space-around;
    align-content: center;
    height: 38px;
    line-height: 38px;
    .van-icon {
      display: flex;
      align-items: center;
    }
  }
}

// 

.tanchu {
  // width: ;
  padding: 8px 8px;
  display: flex;
  flex-direction: column;
  textarea {
    border: none;
  }
  span {
    text-align: right;
  }
  button {
    height: 50px;
    border: none;
    color: #fff;
    background: olive;
  }
}
</style>