<template>
  <div>
    <!--  -->
    <!-- {{ article }} -->
    <div class="head">
      <!-- articleMixinarticle  -->
      <article-content id="content" :article="article"></article-content>
    </div>
    <!--  -->
    <div class="comments">
      <global-list
        :API="API"
        :article_id="article_id"
        :user_id="user_id"
        ref="commentsList"
      >
        <!-- <template v-slot="{list}"> -->
        <template v-slot="{ list }">
          <comment-list
            @replymsg="replymsg"
            :list="list"
            :article="article"
            @isLikeToggleBian="isLikeToggleBian"
          ></comment-list>
        </template>

        <template #finished="{ len }">
          <div>
            <div v-if="len === 0">
              <van-empty
                class="custom-image"
                image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
                description=""
              />
              <!-- <p></p> -->
            </div>
            <div v-else>
              <p></p>
            </div>
          </div>
        </template>
      </global-list>
    </div>

    <!--  -->
    <footer>
      <article-footer
        ref="footer"
        @render="render"
        :preSendIndex="preSendIndex"
        :reply_comment_id="reply_comment_id"
        :placeholder="placeholder"
        :comment_type="comment_type"
        :article="article"
        :article_id="article_id"
        :user_id="user_id"
      ></article-footer>
    </footer>

    <!--  -->
    <van-popup
      v-model="replyShow"
      closeable
      position="bottom"
      :style="{ height: '100%' }"
    >
      <!--  -->
      <div class="replyPopupTop">1</div>
      <!--  -->
      <div class="replyPopupPing">
        <ul>
          <li>
            <div class="ping">
              <div class="pingZuo">
                <img :src="replyInfoBus.info?.avatar" alt="" />
              </div>
              <div class="pingYou">
                <div class="shang">
                  <div>
                    <span>{{ replyInfoBus.info?.nickname }}</span>
                    <!-- <p>{{ replyInfoBus.create_time | timer }}</p> -->
                  </div>
                  <p>
                    <van-icon
                      @click="
                        isLikeToggleBian({
                          comment_id: replyInfoBus._id,
                          isLike: replyInfoBus.is_like,
                          index: replyInfoBus?.index,
                        })
                      "
                      :color="replyInfoBus.is_like ? 'red' : 'gray'"
                      name="good-job"
                    />
                    <span>{{
                      replyInfoBus.like_count > 0 ? replyInfoBus.like_count : ""
                    }}</span>
                  </p>
                </div>
                <p class="pp">{{ replyInfoBus.content }}</p>
                <div class="xia">
                  <div>
                    <a
                      @click="
                        $emit('replymsg', {
                          replyInfoBus: { index, ...replyInfoBus },
                        })
                      "
                      >{{
                        replyInfoBus.reply_num > 0
                          ? replyInfoBus.reply_num
                          : ""
                      }}<van-icon name="arrow"
                    /></a>
                    <!-- <p></p> -->
                  </div>
                  <span>...</span>
                </div>
              </div>
            </div>
          </li>
        </ul>
        <!--  -->
        <div class="comments">
          <global-list
            :API="REPLY_API"
            :article_id="article_id"
            :user_id="user_id"
            :reply_comment_id="reply_comment_id"
            :key="commenListKey"
          >
            <!-- <template v-slot="{list}"> -->
            <template v-slot="{ list }">
              <comment-list
                @replymsg="replymsg"
                :list="list"
                :article="article"
                @isLikeToggleBian="isLikeToggleBian"
              ></comment-list>
            </template>

            <template #finished="{ len }">
              <div>
                <div v-if="len === 0">
                  <van-empty
                    class="custom-image"
                    image="https://img01.yzcdn.cn/vant/custom-empty-image.png"
                    description=""
                  />
                  <!-- <p></p> -->
                </div>
                <div v-else>
                  <p></p>
                </div>
              </div>
            </template>
          </global-list>
        </div>

        <footer>
          <article-footer
            ref="footer"
            @render="render"
            :preSendIndex="preSendIndex"
            :reply_comment_id="reply_comment_id"
            :placeholder="placeholder"
            :comment_type="comment_type"
            :article="article"
            :article_id="article_id"
            :user_id="user_id"
            :is_relay="true"
          ></article-footer>
        </footer>
      </div>
    </van-popup>
  </div>
</template>

<script>
// 
import {
  commentList as API,
  getReplyList as REPLY_API,
} from "@/api/article.js";
// 
import articleMixin from "./articleMixin.js";
// 
import ArticleContent from "./ArticleContent";
// 
import CommentList from "../../components/CommentList/index.vue";

// 
import GlobalList from "../../components/GlobalList/index.vue";

// 
import ArticleFooter from "./ArticleFooter.vue";

import { Empty } from "vant";

import { commentLike, commentUnlike } from "@/api/article";

import { Popup } from "vant";

// 

import html2canvas from "html2canvas";

export default {
  name: "ArticleView",
  mixins: [articleMixin],
  components: {
    // 
    [ArticleContent.name]: ArticleContent,
    //  
    [GlobalList.name]: GlobalList,

    // 
    [CommentList.name]: CommentList,

    // 
    [ArticleFooter.name]: ArticleFooter,

    [Empty.name]: Empty,

    [Popup.name]: Popup,
  },

  data() {
    return {
      API,

      REPLY_API,

      placeholder: undefined, //

      reply_comment_id: "", // id

      comment_type: 0, // 

      preSendIndex: 0, // 

      replyShow: false, ///

      replyInfoBus: {},

      // key   vuekey
      commenListKey: 0,
    };
  },

  mounted() {
    // console.log(this.article_id);
    // console.log(this.$refs.commentsList.list);
  },

  methods: {
    // 
    render() {
      // console.log(111);
      let el = document.querySelector("#content");
      html2canvas(el, {
        // 
        allowTaint: true,
        // 
        useCORS: true,
      }).then(function (canvas) {
        // document.body.appendChild(canvas);
        // console.log(canvas);

        // 

        let ctx = canvas.getContext("2d");
        let img = new Image();

        img.src = require("@/assets/1.png");

        img.onload = function (params) {
          ctx.drawImage(img, 0, 0, 640, 405, 300, 300, 64, 48);
          let url = canvas.toDataURL("image/png");
          console.log(url);
        };

        // let url = canvas.toDataURL("image/png");
        // console.log(url);
      });
    },

    //   /  
    isLikeToggleBian({ comment_id, isLike, index }) {
      console.log(comment_id, isLike, index);
      let api = isLike ? commentUnlike : commentLike;
      let { user_id } = this;
      api({
        comment_id,
        user_id,
      }).then((res) => {
        console.log(res);
        console.log(11111111111111);
        if (res.code === 0) {
          let list = this.$refs.commentsList.list;
          list[index].is_like = !isLike;
          console.log(list[index]);
          list[index].like_count = res.count;
          // 
          this.replyInfoBus.is_like = !isLike;
          this.replyInfoBus.like_count = res.count;
        }
        this.$toast(res.msg);
      });
    },

    //  

    replymsg({ item }) {
      console.log(item);

      console.log(this.replyInfoBus);

      let {
        reply_num,
        info: { nickname },
        _id: reply_comment_id,
        index,
      } = item;
      // console.log(index);

      let list = this.$refs.commentsList.list;

      // key++
      this.commenListKey++;

      // console.log(list[index]);

      if (reply_num === 0) {
        this.$refs.footer.show = true;
      } else {
        this.replyShow = true;
      }
      this.placeholder = "@:" + nickname;
      this.comment_type = 1;
      this.reply_comment_id = reply_comment_id;
      this.preSendIndex = index;
      this.replyInfoBus = {
        index,
        ...list[index],
      };
      console.log(index);
    },
  },
};
</script>

<style lang="less" scoped>
footer {
  height: 64px;
}

.custom-image .van-empty__image {
  width: 90px;
  height: 90px;
}

.replyPopupTop {
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  border-bottom: 1px solid #ccc;
}

.replyPopupPing {
  ul {
    display: flex;
    flex-direction: column;
    li {
      margin-top: 20px;
      border-bottom: 1px solid #efefef;
    }
  }
  .spa {
    margin-top: 30px;
  }
}
.replyPopupPing ul li .ping {
  // height: 90px;
  width: 100%;
  display: flex;
  .pingZuo {
    width: 30px;
    height: 30px;
    overflow: hidden;
    border-radius: 100px;
    margin-right: 10px;
    img {
      width: 100%;
      height: 100%;
    }
  }
}
.pp {
  // width: 90vw;
  // width: 70vm;
  word-wrap: break-word;
}

.replyPopupPing ul li .ping .pingYou {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .shang {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 30px;
    p {
      display: flex;
      height: 35px;
      .active {
        color: red;
      }
    }
  }

  .xia {
    display: flex;
    justify-content: space-between;
    height: 50px;
    align-items: center;
    div {
      display: flex;
      a {
        display: block;
        width: 60px;
        height: 24px;
        background: #d5cbc6;
        border-radius: 25px;
        text-align: center;
        line-height: 24px;
        margin-right: 30px;
      }
    }
  }
}
</style>
